import * as React from "react";
import {DashboardContext} from "../../../Providers";

export interface NodeSettingsDetails {
  html: string;
  script: string;
}

export class NodeSettings extends React.Component<NodeSettingsDetails> {
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  decodeDomProperty(hexx: string) {
    const hex = hexx.toString(); //force conversion
    let str = "";
    for (let i = 0; i < hex.length && hex.substr(i, 2) !== "00"; i += 2)
      str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
    return str;
  }

  render() {
    return (
      <section>
        <script>{eval(this.decodeDomProperty(this.props.script))}</script>
        <div
          dangerouslySetInnerHTML={{
            __html: this.decodeDomProperty(this.props.html)
          }}
        />
      </section>
    );
  }
}
