import * as React from "react";
import css from "./ApplicationTile.module.css";
import Modal from "react-responsive-modal";

export interface ApplicationTileProps {
  name: string;
  appThumbnail: string;
  viewportSize: {
    width: number;
    height: number;
  };
}

const initialApplicationTileState = {isMkAppOpen: false};

export class ApplicationTile extends React.Component<
  ApplicationTileProps,
  typeof initialApplicationTileState
> {
  readonly state = initialApplicationTileState;

  renderThumbnail = () => {
    const {appThumbnail, name} = this.props;
    return (
      <div className={css.applicationThumbnailWrapper}>
        {appThumbnail ? (
          <div className={css.imageThumbnail}>
            <img src={appThumbnail} className={css.imageSize} />
          </div>
        ) : (
          <div className={css.textThumbnail}>
            {name.charAt(0).toUpperCase()}
          </div>
        )}
      </div>
    );
  };

  openMKAppMainView = () => {
    this.setState({isMkAppOpen: true});
  };

  closeMKAppMainView = () => {
    this.setState({isMkAppOpen: false});
  };

  render() {
    const {viewportSize} = this.props;
    const {isMkAppOpen} = this.state;

    return (
      <div
        onDoubleClick={this.openMKAppMainView}
        className={`
          ${css.applicationWrapper} 
          ${css.smartHome}`}
      >
        <div className={css.applicationTitle}>
          {this.renderThumbnail()}
          {this.props.name}
        </div>
        <div className={css.applicationContent}>
          {isMkAppOpen ? (
            <Modal
              closeOnEsc
              classNames={{modal: css.mkAppModalWrapper}}
              onClose={this.closeMKAppMainView}
              open={this.state.isMkAppOpen}
            >
              <iframe
                src=""
                frameBorder="0"
                width={viewportSize.width}
                height={viewportSize.height}
              />
            </Modal>
          ) : (
            <div>put knobs here</div>
          )}
        </div>
      </div>
    );
  }
}
