import * as React from "react";
import css from "./DeviceCard.module.css";
import {DeviceDetails, DeviceStatus} from "../../../../types/internal-types";
import {ApplicationIcon} from "../../../IconWrappers";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars} from "@fortawesome/free-solid-svg-icons";
import {DashboardContext} from "../../../Providers";
import {SkeletonShimmer} from "../../../SkeletonShimmer";
import {Subtitle, CardText} from "./DeviceCardTypography";
import {NodeSettingsDetails, NodeSettings} from "../NodeSettings/NodeSettings";
import Modal from "react-responsive-modal";

export interface OwnProps extends DeviceDetails {
  isExpanded: boolean;
  key: number;
  cardIndex: number;
  toggleExpandCollapse: (cardIndex: number) => void;
}

interface OwnState {
  nodeSettings: NodeSettingsDetails;
  deviceStatus: DeviceStatus | undefined;
  isNodeSettingsLoaded: boolean;
  isNodeSettingsOpenned: boolean;
  isLoading: boolean;
}

export class DeviceCard extends React.Component<OwnProps, OwnState> {
  static contextType = DashboardContext;
  readonly state = {
    nodeSettings: {} as NodeSettingsDetails,
    deviceStatus: {} as DeviceStatus,
    isLoading: true,
    isNodeSettingsLoaded: true,
    isNodeSettingsOpenned: false
  };

  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    const deviceStatus = await this.context.dashboardServerApi.getDeviceStatus(
      this.props.uuid
    );
    this.setState({deviceStatus, isLoading: false});
  }

  handleNodeSettingsModalClick = async () => {
    const nodeSettings: NodeSettingsDetails = {
      html: await this.context.dashboardServerApi.getNodeSettingsHtml(
        this.props.uuid
      ),
      script: await this.context.dashboardServerApi.getNodeSettingsScript(
        this.props.uuid
      )
    };
    this.setState({nodeSettings, isNodeSettingsOpenned: true});
  };

  closeNodeSettingsModal = () => {
    this.setState({isNodeSettingsOpenned: false});
  };

  deviceTypeSection = () => (
    <div
      className={`${css.Section} ${css.deviceContentWrapper} ${css.deviceType}`}
    >
      <div className={css.subTitle}>
        <Subtitle showSkeleton={this.state.isLoading}>DEVICE TYPE</Subtitle>
      </div>
      <CardText showSkeleton={this.state.isLoading}>
        {this.state.deviceStatus.type}
      </CardText>
    </div>
  );

  deviceNameSection = () => {
    const {
      deviceStatus: {description, name},
      isLoading
    } = this.state;
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceName
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={isLoading}>DEVICE NAME</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={isLoading}>
            {name} - {description}
          </CardText>
        </div>
      </div>
    );
  };

  deviceIdSection = () => {
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceName
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={this.state.isLoading}>DEVICE ID</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={this.state.isLoading}>
            {this.state.deviceStatus.uuid}
          </CardText>
        </div>
      </div>
    );
  };

  deviceConnectionSection = () => {
    return (
      <div
        className={`${css.Section} ${css.deviceContentWrapper} ${
          css.deviceStatus
        }`}
      >
        <div className={css.subTitle}>
          <Subtitle showSkeleton={this.state.isLoading}>STATUS</Subtitle>
        </div>
        <div className={css.text}>
          <CardText showSkeleton={this.state.isLoading}>Connected</CardText>
        </div>
      </div>
    );
  };

  deviceOptionsMenu = () => {
    return (
      <div className={`${css.deviceContentWrapper} ${css.deviceSettingsMenu}`}>
        <span className={css.deviceSettingsMenuIcon} onClick={this.handleNodeSettingsModalClick}>
          <FontAwesomeIcon icon={faBars} size="2x" color="#768093" />
        </span>
      </div>
    );
  };

  render() {
    const {isExpanded} = this.props;
    const {isLoading, isNodeSettingsOpenned, nodeSettings} = this.state;

    const expanded = isExpanded ? css.expanded : "";
    return (
      <section className={`${css.cardWrapper} ${expanded}`}>
        <div className={css.cardSummary}>
          <div className={`${css.Section} ${css.IconSectionBackground}`}>
            <ApplicationIcon className={css.deviceTypeIcon} />
          </div>
          {this.deviceTypeSection()}
          {this.deviceNameSection()}
          {this.deviceIdSection()}
          {this.deviceConnectionSection()}
          {this.deviceOptionsMenu()}
          {isLoading && (
            <SkeletonShimmer
              width="1400px"
              color="rgba(249, 250, 251, 0.001)"
            />
          )}
          <Modal
            closeOnEsc
            classNames={{modal: css.mkAppModalWrapper}}
            onClose={this.closeNodeSettingsModal}
            open={this.state.isNodeSettingsOpenned}
          >
            <NodeSettings
              html={nodeSettings.html}
              script={nodeSettings.script}
            />
          </Modal>
        </div>
      </section>
    );
  }
}
