import * as React from "react";
import css from "../SideMenu.module.css";
import {
  FavoritesIcon,
  ApplicationIcon,
  SensorsIcon,
  HelpIcon
} from "../../IconWrappers";
import {SideMenuPageSelectorProps} from "../SideMenu";
import classNames from "classnames";
import {DashboardPages} from "../../../types/internal-types";

export interface OwnProps extends SideMenuPageSelectorProps {}

interface SideMenuLink extends SideMenuPageSelectorProps {
  pageName: DashboardPages;
  children?: React.ReactNode;
}

export class GeneralSection extends React.Component<OwnProps> {
  render() {
    return (
      <section>
        <div className={css.sideMenuTitle}>GENERAL</div>
        <SideMenuLink {...this.props} pageName="FAVORITES_PAGE">
          <FavoritesIcon className={css.sideMenuItemIcon} />
          Favorites
        </SideMenuLink>
        <SideMenuLink {...this.props} pageName="APPLICATION_PAGE">
          <ApplicationIcon className={css.sideMenuItemIcon} />
          applications
        </SideMenuLink>
        <SideMenuLink {...this.props} pageName="SENSORS_PAGE">
          <SensorsIcon className={css.sideMenuItemIcon} />
          Devices
        </SideMenuLink>
        <SideMenuLink {...this.props} pageName="HELP_PAGE">
          <HelpIcon className={css.sideMenuItemIcon} />
          Help
        </SideMenuLink>
      </section>
    );
  }
}

const SideMenuLink = ({
  pageSelector,
  selectedPage,
  children,
  pageName
}: SideMenuLink) => (
  <a
    onClick={() => pageSelector(pageName)}
    className={classNames({
      [css.sideMenuItem]: true,
      [css.selected]: selectedPage === pageName
    })}
  >
    {<>{children}</>}
  </a>
);
