import * as React from "react";
import css from "./Dashboard.module.css";
import {Header} from "../Header/Header";
import SideMenu from "../SideMenu/SideMenu";
import {ApplicationsPage, DevicesPage} from "../DashboardPages";
import {DashboardPages} from "../../types/internal-types";

export interface DashboardProps {}

const innitialDashboardState: DashboardState = {
  selectedDashboardPage: "APPLICATION_PAGE"
};

interface DashboardState {
  selectedDashboardPage: DashboardPages;
}

export class Dashboard extends React.Component<DashboardProps, DashboardState> {
  readonly state = innitialDashboardState;

  selectDashboardPage = (selectedPage: DashboardPages) => {
    this.setState({selectedDashboardPage: selectedPage});
  };

  render() {
    const {selectedDashboardPage} = this.state;
    return (
      <div className={css.root}>
        <Header />
        <main className={css.siteContent}>
          <SideMenu
            pageSelector={this.selectDashboardPage}
            selectedPage={selectedDashboardPage}
          />
          {selectedDashboardPage === "APPLICATION_PAGE" && <ApplicationsPage />}
          {selectedDashboardPage === "SENSORS_PAGE" && <DevicesPage />}
        </main>
      </div>
    );
  }
}
