import {ApplicationTile} from "./ApplicationTile";
import {AppLoader} from "../../AppLoader/AppLoader";
import {MkAppDescription} from "../../../types/internal-types";
import {PageHeader} from "../PageHeader";
import * as React from "react";
import css from "./ApplicationsPage.module.css";
import {ApplicationTileSkeleton} from "../../Skeleton";
import {DashboardContext} from "../../Providers";

const innitialApplicationsPageState = {appList: [] as MkAppDescription[]};

export class ApplicationsPage extends React.Component<
  {},
  typeof innitialApplicationsPageState
> {
  readonly state = innitialApplicationsPageState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  public async componentDidMount() {
    const appList = await this.context.dashboardServerApi.getApplicationsList();
    this.setState({appList});
  }

  render() {
    const {appList} = this.state;
    return (
      <section className={css.pageWrapper}>
        <PageHeader>MY APPLICATIONS</PageHeader>
        <div className={css.pageContentWrapper}>
          {appList &&
            appList.map((mkApp: MkAppDescription, index) => (
              <AppLoader
                key={index}
                mkAppInitData={mkApp}
                render={(loadedProps) => {
                  return loadedProps ? (
                    <ApplicationTile {...loadedProps} />
                  ) : (
                    <ApplicationTileSkeleton />
                  );
                }}
              />
            ))}
        </div>
      </section>
    );
  }
}
