import * as React from "react";
import css from "./Text.module.css";

type Weight = "thin" | "normal" | "bold";

export interface TextProps {
  fontSize: string;
  color: string;
  skeletonColor: string;
  weight: Weight;
  width: string;
  showSkeleton?: boolean;
  children?: JSX.Element | string | number;
}

export const Text: React.SFC<TextProps> = ({
  fontSize,
  color,
  weight,
  width,
  skeletonColor,
  showSkeleton,
  children
}) => {
  const fontWeight = (weight: Weight) => {
    switch (weight) {
      case "thin":
        return {
          fontFamily: '"Open Sans Light", sans-serif',
          weight
        };
      case "normal":
        return {
          fontFamily: '"Open Sans Normal", sans-serif',
          weight
        };
      case "bold":
        return {
          fontFamily: '"Open Sans Bold", sans-serif',
          weight
        };
    }
  };

  const commonStyle = {
    fontSize,
    color,
    display: "inline-block",
    height: fontSize,
    lineHeight: fontSize,
    ...fontWeight(weight)
  };

  const styles = {};

  const skeletonStyles = {
    backgroundColor: skeletonColor,
    content: "",
    width
  };

  return (
    <>
      {showSkeleton ? (
        <div style={{...commonStyle, ...skeletonStyles}} />
      ) : (
        <div style={{...commonStyle, ...styles}}>{children}</div>
      )}
    </>
  );
};
