import {mockAppList, thumbnail} from "../../serverApiMocks";

import {
  DeviceDetails,
  MkAppDescription,
  GetUserNodeListDTO,
  UUID,
  OnGetNodeInfoCallbackDTO,
  DeviceStatus
} from "../../types/internal-types";

export class DashboardServerApi {
  makeSenseApi = window.MkSAPIBuilder.GetInstance();
  nodeInfoCallbackList: {
    [uuid: string]: (response: OnGetNodeInfoCallbackDTO) => void;
  } = {};
  nodeSettingsFilesCallbackList: {
    [uuid: string]: {
      onHtmlFileReturned: (response: any) => void;
      onScriptFileReturned: (response: any) => void;
    };
  } = {};

  OnGetNodeInfoCallback = (response: OnGetNodeInfoCallbackDTO) => {
    this.nodeInfoCallbackList[response.data.payload.uuid](response);
  };

  OnGetFileCallback = (response: any) => {
    const onFileReturned =
      response &&
      response.data &&
      response.data.payload &&
      response.data.payload.file_type === "js"
        ? "onScriptFileReturned"
        : "onHtmlFileReturned";
    this.nodeSettingsFilesCallbackList[response.header.source][onFileReturned](
      response
    );
  };

  constructor() {
    this.makeSenseApi.ConnectGateway();
    this.makeSenseApi.Gateway.OnGatewayDataArrivedCallback = (data: any) => {};
    this.makeSenseApi.Gateway.OnGetNodeInfoCallback = this.OnGetNodeInfoCallback;
    this.makeSenseApi.Gateway.OnGetFileCallback = this.OnGetFileCallback;
    this.makeSenseApi.Gateway.UpdateCallbackTable();
  }

  getApplicationsList = async () => {
    console.log("/applications");
    return Promise.resolve(mockAppList as MkAppDescription[]);
  };

  getApplicationThumbnail = () => {
    console.log("/applications/:appId:/thumbnail");
    return Promise.resolve(thumbnail);
  };

  getDeviceStatus = (uuid: UUID) => {
    const nodeInfoPromise = new Promise<DeviceStatus>((resolve) => {
      this.nodeInfoCallbackList[uuid] = (
        response: OnGetNodeInfoCallbackDTO
      ) => {
        resolve(response.data.payload);
      };
    });
    this.makeSenseApi.GetNodeInfo(uuid);
    return nodeInfoPromise;
  };

  getNodeSettingsScript = (uuid: UUID) => {
    const nodeSettingsDataPromise = new Promise<any>((resolve) => {
      if (this.nodeSettingsFilesCallbackList[uuid]) {
        this.nodeSettingsFilesCallbackList[uuid].onScriptFileReturned = (
          response: any
        ) => {
          resolve(response.data.payload.content);
        };
      } else {
        this.nodeSettingsFilesCallbackList[uuid] = {
          onHtmlFileReturned: (response: any) => {
            resolve(response.data.payload.content);
          },
          onScriptFileReturned: (response: any) => {
            resolve(response.data.payload.content);
          }
        };
      }

      this.makeSenseApi.GetFileContent(uuid, {
        ui_type: "config",
        file_name: "",
        file_type: "js"
      });
    });

    return nodeSettingsDataPromise;
  };

  getNodeSettingsHtml = (uuid: UUID) => {
    const nodeSettingsDataPromise = new Promise<any>((resolve) => {
      if (this.nodeSettingsFilesCallbackList[uuid]) {
        this.nodeSettingsFilesCallbackList[uuid].onHtmlFileReturned = (
          response: any
        ) => {
          resolve(response.data.payload.content);
        };
      } else {
        this.nodeSettingsFilesCallbackList[uuid] = {
          onHtmlFileReturned: (response: any) => {
            resolve(response.data.payload.content);
          },
          onScriptFileReturned: (response: any) => {
            resolve(response.data.payload.content);
          }
        };
      }

      this.makeSenseApi.GetFileContent(uuid, {
        ui_type: "config",
        file_name: "",
        file_type: "html"
      });
    });

    return nodeSettingsDataPromise;
  };

  getDeviceList = () => {
    const deviceListDataPromise = new Promise<DeviceDetails[]>(
      (resolve, reject) => {
        this.makeSenseApi.Webface.GetUserNodeList(
          (response: GetUserNodeListDTO) => {
            const devices = response.nodes.data.map(
              (device: GetUserNodeListDTO["nodes"]["data"][0]) => ({
                uuid: device.uuid
              })
            );
            resolve(devices);
            if (response && response.error) {
              reject("OnGatewayDataArrivedCallback received an error");
            }
          }
        );
      }
    );

    return deviceListDataPromise;
  };
}
