import * as React from "react";
import {Text} from "../../../Text";

export interface OwnProps {
  showSkeleton: boolean;
  children?: string[] | string | number;
}

export const Subtitle: React.SFC<OwnProps> = ({showSkeleton, children}) => (
  <Text
    fontSize="10px"
    color="#768093"
    skeletonColor="#ecedf4"
    weight="bold"
    width="70px"
    showSkeleton={showSkeleton}
  >
    <>{children}</>
  </Text>
);

export const CardText: React.SFC<OwnProps> = ({showSkeleton, children}) => (
  <Text
    color="#2f3148"
    skeletonColor="#ecedf4"
    weight="normal"
    fontSize="14px"
    width="100px"
    showSkeleton={showSkeleton}
  >
    <>{children}</>
  </Text>
);
